header {
  // position: sticky;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  height: var(--nav-height);
}

aside {
  position: fixed;
  top: var(--nav-height);
  left: 0;
  bottom: 0;
  overflow: scroll;
  
  margin-left: var(--side-p);
  width: var(--sidebar-width);
  // prevent scroll "leaking"
  // overscroll-behavior-y: contain;

  // background:red;
}

main {
  padding-top: calc(var(--nav-height) + var(--extra-p) * 1.3);
  padding-left: calc( var(--side-p) + var(--sidebar-width) + var(--extra-p) * 4);
  padding-right: var(--side-p);
  // margin-right: var(--side-p);


  // actial content
  .content {
    // padding: 0 3rem;
    // padding-top: var(--extra-p);
  }
}